import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides, LoadingController, Loading } from 'ionic-angular';
import { MenuController } from 'ionic-angular';
import { PromotionsServiceProvider } from '../../providers/promotions-service/promotions-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the PromotionsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-promotions',
  templateUrl: 'promotions.html',
})
export class PromotionsPage {
  @ViewChild(Slides) slides: Slides;
  loading: Loading;
  promotionsList: any = [];
  activeSlider: number;
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // currency symbol

  constructor(public navCtrl: NavController, public navParams: NavParams, private promotionsProvider: PromotionsServiceProvider, private hotelProvider: HotelServiceProvider, public menuCtrl: MenuController, public loadingCtrl: LoadingController, private notificationProvider: NotificationProvider) {

    if (this.promotionsProvider.objPromotions && this.promotionsProvider.objPromotions.activeSlider) {
      this.activeSlider = this.promotionsProvider.objPromotions.activeSlider;
    }
    //get promotion list
    this.promotionsList = this.promotionsProvider.objPromotions.data;
  }

  ionViewWillLeave() {
    //save active slider index
    this.promotionsProvider.objPromotions.activeSlider = this.slides.getActiveIndex();
  }

  ionViewDidLoad() {
    if (this.activeSlider) {
      setTimeout(() => {
        this.slides.slideTo(this.activeSlider, 0);
      }, 200);
    }
  }

  //moves to next slide
  slideNext() {
    this.slides.slideNext();
  }

  //moves to previous slide
  slidePrev() {
    this.slides.slidePrev();
  }

  fnGotoDetailsPage(itemId) {
    this.navCtrl.push("PromotionsDetailsPage", { itemId: itemId }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
}



